import React from "react";
import DropBasket from "./DropBasket";

import {
	Button,
	ButtonGroup,
	AppBar,
	Box,
	Toolbar,
	IconButton,
	Typography,
	Menu,
	Container,
	MenuItem,
} from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";

export default function Header({
	basket,
	addBasket,
	delBasketEl,
	submitMessage,
	scrollTo,
}) {
	const [anchorElNav, setAnchorElNav] = React.useState(null);

	const handleOpenNavMenu = (event) => {
		setAnchorElNav(event.currentTarget);
	};

	const handleCloseNavMenu = () => {
		setAnchorElNav(null);
	};

	return (
		<AppBar sx={{ position: "sticky", top: 0, mb: 2 }} elevation={4}>
			<Container maxWidth="xl">
				<Toolbar disableGutters>
					<Box sx={{ flexGrow: 1, display: { xs: "flex", md: "none" } }}>
						<IconButton
							size="large"
							aria-label="account of current user"
							aria-controls="menu-appbar"
							aria-haspopup="true"
							onClick={handleOpenNavMenu}
							color="inherit"
						>
							<MenuIcon />
						</IconButton>
						<Menu
							id="menu-appbar"
							anchorEl={anchorElNav}
							anchorOrigin={{
								vertical: "bottom",
								horizontal: "left",
							}}
							keepMounted
							transformOrigin={{
								vertical: "top",
								horizontal: "left",
							}}
							open={Boolean(anchorElNav)}
							onClose={handleCloseNavMenu}
							sx={{
								display: { xs: "block", md: "none" },
							}}
						>
							<MenuItem>
								<Typography
									textAlign="center"
									onClick={() => scrollTo("Main")}
								>
									
								</Typography>
							</MenuItem>
							<MenuItem>
								<Typography
									textAlign="center"
									onClick={() => scrollTo("Catalog")}
								>
									
								</Typography>
							</MenuItem>
							<MenuItem>
								<Typography
									textAlign="center"
									onClick={() => scrollTo("About")}
								>
									 
								</Typography>
							</MenuItem>
						</Menu>
					</Box>

					<Box sx={{ flexGrow: { xs: "1", md: "1" } }}>
						<img
							src="logo.svg"
							alt="logo"
							onClick={() => scrollTo("Main")}
						/>
					</Box>
					<ButtonGroup
						sx={{ display: { xs: "none", md: "flex" }, mr: 5 }}
						color="textColor"
						variant="text"
						aria-label="text button group"
					>
						<Button onClick={() => scrollTo("Main")}></Button>
						<Button onClick={() => scrollTo("Catalog")}></Button>
						<Button onClick={() => scrollTo("About")}> </Button>
					</ButtonGroup>
					<DropBasket
						basket={basket}
						addBasket={addBasket}
						delBasketEl={delBasketEl}
						submitMessage={submitMessage}
					/>
				</Toolbar>
			</Container>
		</AppBar>
	);
}
