import * as React from "react";
import FormDialog from "./FormDialog";

import {
	Box,
	Paper,
	Divider,
	Typography,
	Card,
	Badge,
	Menu,
	Avatar,
	CardHeader,
	IconButton,
	Tooltip,
} from "@mui/material";

import RemoveCircleOutlineOutlinedIcon from "@mui/icons-material/RemoveCircleOutlineOutlined";
import AddCircleOutlineOutlinedIcon from "@mui/icons-material/AddCircleOutlineOutlined";
import DeleteIcon from "@mui/icons-material/Delete";
import ShoppingBasketOutlinedIcon from "@mui/icons-material/ShoppingBasketOutlined";

export default function DropBasket({
	basket,
	addBasket,
	delBasketEl,
	submitMessage,
}) {
	const [anchorEl, setAnchorEl] = React.useState(null);
	const open = Boolean(anchorEl);
	const handleClick = (event) => {
		setAnchorEl(event.currentTarget);
	};
	const handleCloseDropBasket = () => {
		setAnchorEl(null);
	};

	return (
		<div>
			<Badge badgeContent={basket.length} color="success">
				<IconButton
					onClick={handleClick}
					sx={{
						m: -1.5,
					}}
				>
					<ShoppingBasketOutlinedIcon
						sx={{
							fontSize: 30,
						}}
						color="textColor"
					/>
				</IconButton>
			</Badge>

			<Menu
				anchorEl={anchorEl}
				id="account-menu"
				open={open}
				sx={{ backgroundColor: "rgba(229, 229, 229, 0.6);" }}
				onClose={handleCloseDropBasket}
			>
				<Box sx={{ width: 300 }}>
					<Paper sx={{ position: "sticky", top: 0, zIndex: 10 }}>
						<Typography sx={{ textAlign: "center", p: 1 }}>
							
						</Typography>
						<Divider />
					</Paper>

					{basket.length === 0 ? (
						<Typography sx={{ textAlign: "center", p: 1 }}>
							 
						</Typography>
					) : (
						<Box>
							{basket.map((basketEl) => (
								<Card key={basketEl.item[0]} sx={{ m: 1 }}>
									<CardHeader
										avatar={
											<Avatar
												sx={{ width: 54, height: 54 }}
												aria-label="recipe"
												src={basketEl.item[4]}
											></Avatar>
										}
										title={basketEl.item[1]}
										subheader={basketEl.item[2]}
									/>
									<Box
										sx={{
											display: "flex",
											gap: 2,
											width: "100%",
											justifyContent: "center",
											mb: 1,
										}}
									>
										<Tooltip title="Delete">
											<IconButton
												onClick={() => delBasketEl(basketEl.item)}
											>
												{basketEl.count === 1 ? (
													<DeleteIcon />
												) : (
													<RemoveCircleOutlineOutlinedIcon />
												)}
											</IconButton>
										</Tooltip>
										<Typography sx={{ textAlign: "center", p: 1 }}>
											{basketEl.count}
										</Typography>
										<Tooltip title="Add">
											<IconButton
												onClick={() => addBasket(basketEl.item)}
											>
												<AddCircleOutlineOutlinedIcon />
											</IconButton>
										</Tooltip>
									</Box>
								</Card>
							))}
							<Paper
								sx={{
									position: "sticky",
									bottom: 0,
									zIndex: 10,
									p: 0.01,
								}}
							>
								<Divider />
								<FormDialog
									basket={basket}
									submitMessage={submitMessage}
									handleCloseDropBasket={handleCloseDropBasket}
								/>
							</Paper>
						</Box>
					)}
				</Box>
			</Menu>
		</div>
	);
}
