import React from "react";
import { Typography, Container, Paper } from "@mui/material";

export default function About() {
	return (
		<>
			<Paper
				sx={{
					mt: 4,
				}}
				elevation={12}
				square
			>
				<Container
					maxWidth="sm"
					sx={{
						py: { xs: 5, md: 10, lg: 15 },
						px: 5,
						textAlign: "center",
					}}
				>
					<Typography variant="h4"> </Typography>
					<Typography
						sx={{
							mt: 5,
						}}
					>
						Lorem ipsum dolor sit amet consectetur adipisicing elit. Non
						obcaecati dolorem laborum repellat? Iure perferendis ipsum a
						consequuntur in voluptas!
					</Typography>
				</Container>
			</Paper>
		</>
	);
}
