import React, { useEffect, useState } from "react";
import axios from "axios";
import { Element, scroller, animateScroll as scroll } from "react-scroll";

import Alert from "@mui/material/Alert";
import Dialog from "@mui/material/Dialog";

import Header from "./Header";
import Cards from "./Cards";
import Hello from "./Hello";
import About from "./About";

export default function App() {
	const [basket, setBasket] = useState([]);
	const [cards, setCards] = useState(null);

	const addBasket = (el) => {
		const basketElement = basket.find((element) => element.item[0] === el[0]);
		if (basket.length === 0) {
			setBasket([...basket, { item: el, count: 1 }]);
		} else if (basketElement) {
			setBasket((prevState) =>
				prevState.map((prevEl) =>
					prevEl.item[0] === el[0] // item.id === el.id
						? { ...prevEl, count: prevEl.count + 1 }
						: prevEl
				)
			);
		} else {
			setBasket([...basket, { item: el, count: 1 }]);
		}
	};

	const delBasketEl = (el) => {
		const basketElement = basket.find((element) => element.item[0] === el[0]);

		setBasket((prevState) =>
			prevState.filter((prevEl) =>
				prevEl.count === 1
					? prevEl.item[0] !== basketElement.item[0]
					: "qqq"
			)
		);

		setBasket((prevState) =>
			prevState.map((prevEl) =>
				prevEl.item[0] === el[0] // item.id === el.id
					? { ...prevEl, count: prevEl.count - 1 }
					: prevEl
			)
		);
	};

	const submitMessage = () => {
		const TOKEN = "6278851300:AAHEwQBj6GQ34KS-vQ6_L5ej_g--UQO6N8A";
		const CHAT_ID = "-1001807655173";
		const URI_API = `https://api.telegram.org/bot${TOKEN}/sendMessage`;
		const currentBasket = basket.map((item) => [item.item[1], item.count]);
		const currentBasket1 = basket.map(
			(item) =>
				`<b>:</b>${item.item[1]}|<b>:</b>${item.count}\n`
		);
		document
			.getElementById("formMessage")
			.addEventListener("submit", function (e) {
				e.preventDefault();
				let message = `<b>  :</b>\n`;
				message += `<b>: </b>${this.name.value}\n`;
				message += `<b>: </b>${this.phone.value}\n`;
				message += `<b>: </b>${this.telegram.value}\n`;
				message += basket.map(
					(item) =>
						`\n<b>: </b>${item.item[1]}|<b>: </b>${item.count}`
				);

				console.log(basket);
				console.log(currentBasket);
				console.log(currentBasket1);

				console.log(message);
				axios
					.post(URI_API, {
						chat_id: CHAT_ID,
						parse_mode: "html",
						text: message,
					})
					.then((res) => {
						this.name.value = "";
						this.phone.value = "";
						this.telegram.value = "";
						setBasket([]);
						handleClickOpen();
					})
					.catch((err) => {
						console.warn(err);
					})
					.finally(() => {
						console.log("finally");
					});
			});
	};

	useEffect(() => {
		const apiUrl =
			"https://script.google.com/macros/s/AKfycbxV9vBqnj19lv8rrNP1CHq4Jz6UIv2ohHcj3KGLcKH_WhnhmiCI1SL6QIFhS16kMeXw/exec";
		axios.get(apiUrl).then((resp) => {
			const allProducts = resp.data;
			setCards(allProducts);
		});
	}, [setCards]);

	const [open, setOpen] = useState(false);

	const handleClickOpen = () => {
		setOpen(true);
	};

	const handleClose = () => {
		setOpen(false);
	};

	const scrollTo = (id) => {
		if (id === "Main") scroll.scrollToTop();

		if (id === "Catalog") {
			scroller.scrollTo("Catalog", {
				duration: 1500,
				delay: 100,
				smooth: true,
			});
		}

		if (id === "About") {
			scroller.scrollTo("About", {
				duration: 1500,
				delay: 100,
				smooth: true,
			});
		}
	};

	return (
		<>
			<Header
				basket={basket}
				addBasket={addBasket}
				delBasketEl={delBasketEl}
				submitMessage={submitMessage}
				scrollTo={scrollTo}
			/>

			<Hello />

			<Element id="Catalog">
				<Cards cards={cards} addBasket={addBasket} />
			</Element>

			<Element id="About">
				<About />
			</Element>

			<Dialog
				open={open}
				onClose={handleClose}
				aria-labelledby="alert-dialog-title"
				aria-describedby="alert-dialog-description"
			>
				<Alert severity="success"> !</Alert>
			</Dialog>
		</>
	);
}
