import React, { useState } from "react";
import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";

export default function FormDialog({
	basket,
	submitMessage,
	handleCloseDropBasket,
}) {
	const [open, setOpen] = useState(false);

	const handleClickOpen = () => {
		setOpen(true);
	};

	const handleCloseForm = () => {
		setOpen(false);
	};

	return (
		<div>
			<Button
				onClick={handleClickOpen}
				variant="filledTonal"
				sx={{
					border: 1,
					m: 1,
					display: "block",
					width: 285,
				}}
			>
				
			</Button>

			<Dialog
				open={open}
				onClose={handleCloseForm}
				component="form"
				noValidate
				autoComplete="on"
				id="formMessage"
			>
				<DialogTitle> </DialogTitle>

				<DialogContent>
					<DialogContentText>
						     
					</DialogContentText>
					<TextField
						autoFocus
						margin="dense"
						id="name"
						label=" "
						type="name"
						fullWidth
					/>
					<TextField
						margin="dense"
						id="phone"
						label=" "
						type="phone"
						fullWidth
					/>
					<TextField
						margin="dense"
						id="telegram"
						label="  Telegram"
						type="telegram"
						fullWidth
					/>
				</DialogContent>
				<DialogActions>
					<Button
						onClick={handleCloseForm}
						color="textColor"
						sx={{
							border: 1,
							m: 1,
						}}
					>
						
					</Button>
					<Button
						onClick={() => {
							submitMessage();
							handleCloseDropBasket();
						}}
						type="submit"
						color="textColor"
						sx={{
							border: 1,
							m: 1,
						}}
					>
						
					</Button>
				</DialogActions>
			</Dialog>
		</div>
	);
}
