import React from "react";

import {
	Card,
	CardActions,
	CardContent,
	CardMedia,
	Button,
	Typography,
} from "@mui/material";

export default function CardsItem({ card, addBasket }) {
	return (
		<Card elevation={3}>
			<CardMedia
				sx={{ height: { xs: 250, md: 250, lg: 300 } }}
				component="img"
				fit="contain"
				image={card[4]}
				alt="img"
			/>
			<CardContent sx={{ px: 2 }}>
				<Typography gutterBottom variant="h5" component="div">
					{card[1]}
				</Typography>
				<Typography gutterBottom variant="body2" color="text.secondary">
					{card[2]}
				</Typography>
			</CardContent>
			<CardActions
				sx={{
					display: "flex",
					justifyContent: "space-between",
					p: 1.5,
				}}
			>
				<Typography variant="h6" color="text.secondary">
					: {card[3]}
				</Typography>
				<Button
					onClick={() => addBasket(card)}
					size="small"
					color="textColor"
					sx={{ p: 1, border: 1 }}
				>
					 
				</Button>
			</CardActions>
		</Card>
	);
}
