import React, { useState, useEffect } from "react";
import CardsItem from "./CardsItem";
import SkeletonItem from "./SkeletonItem";
import Catalog from "./Catalog";

import { Button, Container, Grid } from "@mui/material";

export default function Cards({ cards, addBasket }) {
	const [currentlyCategory, setCurrentlyCategory] = useState(null);

	useEffect(() => {
		if (cards !== null) {
			setCurrentlyCategory(cards.result.catalog);
		}
	}, [cards]);

	const currentlyCategoryCatalog = () => {
		setCurrentlyCategory(cards.result.catalog);
	};

	const updateCurrentlyCategory = (value) => {
		setCurrentlyCategory(cards.result[value]);
	};

	return (
		<Container maxWidth="xl">
			{currentlyCategory !== null &&
			currentlyCategory !== cards.result.catalog ? (
				<Button
					onClick={() => currentlyCategoryCatalog()}
					variant="h2"
					color="text.secondary"
					sx={{
						border: 1,
						my: 2,
						display: "block",
						width: "100%",
						height: 80,
					}}
				>
					
				</Button>
			) : null}

			<Grid
				spacing={4}
				container
				direction="row"
				justifyContent="center"
				alignItems="stretch"
			>
				{currentlyCategory === null ? (
					<>
						{[1, 2, 3].map((item) => (
							<Grid item key={item} xs={10} sm={5.5} md={4} lg={3.5}>
								<SkeletonItem />
							</Grid>
						))}
					</>
				) : (
					<>
						{currentlyCategory.map((card) => (
							<Grid item key={card[0]} xs={10} sm={5.5} md={4} lg={3.5}>
								{currentlyCategory === cards.result.catalog ? (
									<Catalog
										card={card}
										updateCurrentlyCategory={updateCurrentlyCategory}
									/>
								) : (
									<CardsItem card={card} addBasket={addBasket} />
								)}
							</Grid>
						))}
					</>
				)}
			</Grid>
		</Container>
	);
}
